import * as Yup from "yup";

export const editWaterSchema = Yup.object({
  waterAmount: Yup.string()
    .required("Required")
    .max(15, "Must be 15 characters or less")
    .matches(/\d{2,}/g),
  date: Yup.string()
    .required("Required")
    .matches(/^(\d{2}:\d{2})$/, {
      message: "Entered date must match following format hh:mm (10:25)",
    }),
});
export const editWaterSchemaUK = Yup.object({
  waterAmount: Yup.string()
    .required("'")
    .max(15, "    15 ")
    .matches(/\d{2,}/g),
  date: Yup.string()
    .required("'")
    .matches(/^(\d{2}:\d{2})$/, {
      message:
        "      : (10:25)",
    }),
});
